import React, { useState } from 'react';
import '../../../node_modules/@fortawesome/fontawesome-free/css/all.min.css'; 
import './Calculator.css';

function Calculator() {

    const [result, setResult] = useState("");

    function handleClick(e){
        setResult (result.concat(e.target.name));
    }

    function backspace(){
        setResult (result.slice(0, result.length-1));
    }

    function clear(){
        setResult ("");
    }

    function calculate(){
        try{
            setResult(eval(result).toString());
        } catch (error){
            setResult("Error");
        }
    }


    return (
        <div className="main-wrap">
            <div className="calc-box">
            <div className="result-box">
                <input type="text" value={result} />
            </div>
            <div className="keypad-box">
                <button id="clear" onClick={clear}>AC</button>
                <button id="backspace" onClick={backspace}><i className="fas fa-backspace"></i></button>
                <button name="%" onClick={handleClick} >%</button>
                <button name="/" onClick={handleClick} >/</button>
                <button name="7" className="numbers" onClick={handleClick} >7</button>
                <button name="8" className="numbers" onClick={handleClick} >8</button>
                <button name="9" className="numbers" onClick={handleClick} >9</button>
                <button name="*" onClick={handleClick} >&times;</button>
                <button name="4" className="numbers" onClick={handleClick} >4</button>
                <button name="5" className="numbers" onClick={handleClick} >5</button>
                <button name="6" className="numbers" onClick={handleClick} >6</button>
                <button name="-" onClick={handleClick} >-</button>
                <button name="1" className="numbers" onClick={handleClick} >1</button>
                <button name="2" className="numbers" onClick={handleClick} >2</button>
                <button name="3" className="numbers" onClick={handleClick} >3</button>
                <button name="+" onClick={handleClick} >+</button>
                <button name="." className="numbers" onClick={handleClick} >.</button>
                <button name="0" className="numbers" onClick={handleClick} >0</button>
                <button id="result" onClick={calculate}>=</button>
            </div>
            </div>
        </div>
    )
}

export default Calculator;